<template>
  <div class="my-box">
    <div class="my-top-box" :class="disc === 'fictitious'?'my-top-box1':''">
      <div class="my-top" :style="{backgroundImage: 'url('+imageUrl+')'}">
        <img src="../../assets/my/myPeople.png" class="my-people" v-if="disc !== 'fictitious'"/>
        <div class="my-bar">
          <van-icon class="bar-right" :color="this.disc === 'fictitious'? '#222222':'#fff'" name="setting-o" @click="linkMyManage" />
          <img src="../../assets/hcLogo.png" class="my-bar-logo"/>
          <div class="my-news fictitious-top" v-if="this.disc === 'fictitious'">
            <div>模拟账户{{account}}</div>
<!--            <div>{{phone}}</div>-->
          </div>
          <div class="my-news" v-else>
            <div>{{name}}</div>
            <div>{{account}}</div>
          </div>
        </div>
        <div class="my-content" v-if="disc !== 'fictitious'">
          <div class="my-news-bottom">
            <div  style="width: 50%">
              <div>{{$t('balance')}}
                <van-icon name="eye-o" v-show="showMoney === 0" @click="showMoneyOff(1)"/>
                <van-icon name="closed-eye" v-show="showMoney === 1" @click="showMoneyOff(0)"/>
              </div>
              <div>{{balance}}</div>
            </div>
            <div style="width: 50%;float: right;text-align: center;" class="rate">
              <div>{{$t('FrozenFunds')}}</div>
              <div>{{ProfitToday}}</div>
            </div>
          </div>
          <div class="my-news-bottom-but">
            <van-button  style="width: 50%" plain class="withdrawal" @click="getWithdrawal">{{$t('withdraw')}}</van-button>
            <van-button  style="width: 50%" @click="getPayment">{{$t('Recharge')}}</van-button>
          </div>
        </div>
        <div v-else class="trade-top">
          <div class="top-tap">模拟</div>
          <p>模拟资金($)</p>
          <p>{{balance}}</p>
        </div>
      </div>
    </div>
    <div class="tool-box">
      <div v-if="$i18n.locale ==='zh'">
        <div class="notice-my" @click="golinkStatus" :style="{backgroundImage: 'url('+inAudit+')'}" v-if="showOpenTips === 3">
          <div>
            <div class="content">
              <div class="notice-my-top">开户审核中...</div>
              <div class="notice-my-bot">预计3个工作日内反馈审核结果,请耐心等候</div>
            </div>
          </div>
        </div>
        <div class="notice-my" @click="golinkStatus" :style="{backgroundImage: 'url('+bgImg+')'}" v-if="noticeOff === 0">
          <div>
            <div class="content">
              <div class="notice-my-top">极速开户 一键投资</div>
              <div class="notice-my-bot">您还未开户，请尽快申请</div>
            </div>
          </div>
        </div>
        <div class="notice-my" @click="golinkStatus" :style="{backgroundImage: 'url('+bgImg+')'}" v-if="noticeOff === 1">
          <div>
            <div class="content1">
              <div class="notice-my-top">开户审核通过</div>
              <div class="notice-my-bot1">为了资金安全,请尽快设置资金密码</div>
            </div>
          </div>
        </div>
        <div class="notice-my" @click="golinkStatus" :style="{backgroundImage: 'url('+bgImg+')'}" v-if="noticeOff === -1">
          <div >
            <div class="content2">
              <div class="notice-my-top">开户审核未通过</div>
              <div class="notice-my-bot">
                您提交的身份信息错误，请您重新<br>提交开户申请
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="my-tool">
        <div @click="linkMyNews">
          <img src="../../assets/my/myNews.png" class="tool-icon"/>
          <div class="wallet-title">{{$t('UserInfo')}}</div>
          <van-icon name="arrow" />
        </div>
        <div @click="linkCapitalRecord" v-if="this.disc !== 'fictitious'">
          <img src="../../assets/my/capitalRecord.png" class="tool-icon"/>
          <div class="wallet-title">{{$t('FundingRecords')}}</div>
          <van-icon name="arrow" />
        </div>
        <div style="border-top:none;" v-if="this.disc !== 'fictitious' && status !== '1' && this.isDrawPass === '1'" @click="linkOpen">
          <img class="tool-icon" src="../../assets/my/openAccount.svg"/>
          <div class="wallet-title">{{$t('FuturesAO')}}</div>
          <van-icon name="arrow" />
        </div>
        <div @click="linkLoginPass" v-if="this.disc !== 'fictitious'">
          <img src="../../assets/my/loginPass.png" class="tool-icon"/>
          <div class="wallet-title">{{$t('ModifyLoginPassword')}}</div>
          <van-icon name="arrow" />
        </div>
        <div @click="linkFundPass"  v-if="this.disc !== 'fictitious'">
          <img src="../../assets/my/FundPass.png" class="tool-icon"/>
          <div class="wallet-title">{{$t('ChangeFundPassword')}}</div>
          <van-icon name="arrow" />
        </div>
        <!--      <div @click="linkShare">-->
        <!--        <img src="../../assets/my/Invitation.svg" class="tool-icon"/>-->
        <!--        <div class="wallet-title">邀请好友</div>-->
        <!--        <van-icon name="arrow" />-->
        <!--      </div>-->
<!--        <div @click="linkProblem">-->
<!--          <img src="../../assets/my/cProblem.png" class="tool-icon"/>-->
<!--          <div class="wallet-title">常见问题</div>-->
<!--          <van-icon name="arrow" />-->
<!--        </div>-->
        <!--      <div @click="callPhone">-->
        <!--        <img src="../../assets/my/service.svg" class="tool-icon"/>-->
        <!--        <div class="wallet-title">联系客服</div>-->
        <!--        <van-icon name="arrow" />-->
        <!--      </div>-->
      </div>
    </div>

    <router-view></router-view>
  </div>

</template>

<script>
  import { Toast } from 'vant'
  import EP from '../../utils/encryption'
  import '../../assets/font/iconfont'
export default {
  name: 'my',
  data () {
    return {
      bgImg:'',
      // 背景图片
      imageUrl: require('../../assets/my/my_bg.png'),
      // 提交
      submission:require('../../assets/my/submission.png'),
      // 不通过
      noAdopt:require('../../assets/my/noAdopt.png'),
      // 通过
      adopt:require('../../assets/my/adopt.png'),
      // 审核中
      inAudit:require('../../assets/my/inAudit.png'),
      // 判断背景图
      showOpenTips:'',
      // 盘
      disc:'',
      // 审核状态
      status:'',
      isDrawPass:'',
      // 余额
      balance:0,
      showMoney:0,
      // 账号
      account:'',
      // 手机号码
      phone:'',
      // 盘url
      discUrl:'',
      name:this.$t('Unnamed'),
      // 用户id
      uid:'',
      // 开户状态
      noticeOff:'',
      linkStatus:'',
      // 查询余额计时器
      balanceTime:null,
      websock:null,
      // 今日盈利
      ProfitToday:0,
      // 汇率
      rate:0
    }
  },
  created(){
    let that = this
    this.disc = localStorage.getItem("environment");
    localStorage.duan = 'unDuan'
    if(this.disc === 'fictitious'){
      this.imageUrl = ''
    }else {
      this.imageUrl = require('../../assets/my/my_bg.png')
    }
    this.status = localStorage.getItem("status")
    this.isDrawPass = localStorage.getItem("isDrawPass")
    if(localStorage.getItem("name")){
      this.name =localStorage.getItem("name")
    }else {
      this.name = this.$t('Unnamed')
    }
    this.account = localStorage.getItem("account")
    this.phone = localStorage.getItem("phone")
    this.discUrl = localStorage.getItem('discUrl')

    // this.discUrl = '192.168.5.6'
    this.uid = localStorage.getItem('uid')
    if(this.status === '3'){
      that.showOpenTips = 3
    }else {
      if(this.status === '0'){
        this.noticeOff = 0
        that.showOpenTips = -3
        this.bgImg = this.submission
      }else if(this.status === '-1'){
        that.showOpenTips = -3
        this.noticeOff = -1
        this.bgImg = this.noAdopt
      }else if(this.status === '1'){
        if(this.status === '1' && this.isDrawPass === '0'){
          this.noticeOff = 1
          that.showOpenTips = -3
          this.bgImg = this.adopt
        }else {
          this.noticeOff = ''
          that.showOpenTips = ''
        }
      }
    }
    document.addEventListener("plusready", function () {
      // 运行环境从前台切换到后台事件
      let hiddenTime = ''
      let visibleTime = ''
      document.addEventListener("pause", function () {
        hiddenTime = new Date().getTime()
      }, false);
      // 运行环境从后台切换到前台事件
      document.addEventListener("resume",function () {
        console.log('亮屏了')
        visibleTime = new Date().getTime();
        if((visibleTime - hiddenTime) / 1000 > 30 && that.$route.name === 'my'){
          clearInterval(that.balanceTime);
        }
      }, false);
    }, false);
    this.handdleMsg()
  },
  beforeDestroy() {
    if(this.balanceTime) {
      clearInterval(this.balanceTime);
    }
  },
  methods: {
    // 修改登陆密码
    linkLoginPass(){
      this.$router.push({
        name: 'myNewPassword'
      })
    },
    // 修改资金密码
    linkFundPass () {
      console.log(this.isDrawPass)
      let that = this
      if(this.isDrawPass === '1'){
        this.$router.push({
          name: 'modifyFundPass',
          params:{
            modify:'accountManage',
          }
        })
      }else {
        Toast({
          position:'bottom',
          message:that.$t('noUnnamed'),
          duration:1000,
        })
      }

    },
    // 用户信息
    linkMyNews(){
      this.$router.push({
        name: 'myNews'
      })
    },
    getWithdrawal(){
      let token =  localStorage.getItem('token')
      let that = this
      // this.$axios.get('http://'+this.discUrl+':7777/api/pay/configs?userId='+this.uid+'&token='+token).then(res =>{
      //   console.log(res)
      if(this.status === '1' && this.isDrawPass === '1'){
        this.$router.push({
          name:'withdrawal'
        })
      }
      if(this.status !== '1'){
        Toast({
          message:that.$t('noUnnamed'),
          duration:3000,
          position:'bottom'
        })
      }
      if(this.status === '1' && this.isDrawPass !== '1'){
        Toast({
          message:that.$t('AuditResults4'),
          position:'bottom',
          onClose(){
            that.$router.push({
              name:'setFundPassword'
            })
          }
        })
      }
      //
      // })
    },
    // 充值
    getPayment(){
      let token =  localStorage.getItem('token')
      let that = this
      // this.$axios.get('http://'+this.discUrl+':7777/api/pay/configs?userId='+this.uid+'&token='+token).then(res =>{
      //   console.log(res)
      //   if(res.data.object != null){
          that.$router.push({
            name:'recharge'
          })
        // }else {
        //   Toast({
        //     message:res.data.msg,
        //     duration:2000,
        //     position:'bottom'
        //   })
        // }

      // })
    },
    // 跳转到我的钱包-资金记录
    linkCapitalRecord () {
      this.$router.push({path: '/capitalRecord'})
    },
    getStatusBarHeight(){
      var immersed = 0;
      var ms=(/Html5Plus\/.+\s\(.*(Immersed\/(\d+\.?\d*).*)\)/gi).exec(navigator.userAgent);
      if(ms&&ms.length>=3){ // 当前环境为沉浸式状态栏模式
        immersed=parseFloat(ms[2]);// 获取状态栏的高度
      }
      return immersed;
    },
    // 钱显示隐藏
    showMoneyOff(idx){
      this.showMoney = idx
      if(this.showMoney === 1){
        this.balance = '******'
        this.ProfitToday = '******'
        clearInterval(this.balanceTime);
      }else {
        this.handdleMsg()
      }
    },
    // getRate(money){
    //   // this.optionalNum = '￥'+((this.redata.data.object.balance*this.rate).toFixed(2))
    //   let rate = 0
    //   this.$axios.get('http://'+this.discUrl+':7777/api/systemconfig').then(res =>{
    //     if(res.data.code === 0){
    //       rate = '￥'+((money*Number(res.data.object.money_in_out_rate)).toFixed(2))
    //       this.optionalNum = rate
    //     }
    //   })
    // },
    getDay(day){
      let today = new Date();
      let targetday_milliseconds=today.getTime() + 1000*60*60*24*day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      let tYear = today.getFullYear();
      let tMonth = today.getMonth();
      let tDate = today.getDate();
      tMonth = this.doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return tYear+"-"+tMonth+"-"+tDate;
    },
    doHandleMonth(month){
      let m = month;
      if(month.toString().length == 1){
        m = "0" + month;
      }
      return m;
    },
    // 通知栏
    golinkStatus(){
      // console.log(this.status)
      // console.log(this.isDrawPass)
      if(this.status === '1' && this.isDrawPass === '0'){
        this.$router.push({
          name:'adopt',
          params:{
            modify:'my'
          }
        })
      }else if(this.status === '0'){
        this.$router.push({
          name:'register'
        })
      }else if(this.status === '-1'){
        this.$router.push({
          name:'register'
        })
      }
    },
    // 获取余额
    handdleMsg(msg) {
      let that = this;
      if (that.global.ws && that.global.ws.readyState === 1 && localStorage.duan !== 'duan') {
        // console.log("发送信息", msg);
        that.balanceTimeData()
        clearInterval(that.balanceTime);
        that.balanceTime = setInterval(that.balanceTimeData,5000)
      }
      that.global.ws.onmessage = function(res) {
        that.websocketonmessage (res)
      };
    },
    balanceTimeData(){
      this.global.ws.onopen = this.onopen()
    },
    onopen () { // websocket连接后发送数据(send发送)
      let actions;
      let that = this
        actions = {
          action: "loadusermoney",
          reqID: 17,
        }
      that.global.ws.send(JSON.stringify(actions))
    },
    websocketonerror () { // 连接建立失败重连
      this.getLogin()
    },
    websocketonmessage (e) { // 数据接收
      let that = this
      this.redata = JSON.parse(e.data)
      console.log(this.redata,'我的')
      if (this.redata.action === "discarded") {
        if(this.redata.data.code === 0){
          Toast({
            duration:1000,
            message:that.$t('noNetwork'),
            position:'bottom',
            onClose() {
              that.Logout()
            }
          })
        }
      }
      if(this.redata.action === "mutipointlogin"){
        Toast({
          message:that.$t('jixMessage'),
          position:'bottom',
          duration:2000,
          onOpened() {
            localStorage.jix = 'jix'
            that.Logout()
          }
        })
      }else {
        if(this.redata.data.code === 0 && this.redata.action === 'loadusermoney'){
          this.balance = '$'+(this.redata.data.object.balance)

          this.ProfitToday = '$'+(this.redata.data.object.frozenCash)
        }
        if(this.redata.action === 'login' && this.redata.data.code === 0){
          localStorage.setItem('token',this.redata.data.object.token)
          this.handdleMsg()
        }
        if(this.redata.action === "checkResult"){
          if(this.redata.data.code === 0 && this.redata.action === "checkResult" && this.redata.data.object !== "-1"){
            localStorage.setItem('status','1')
            Toast({
              type:'success',
              message:that.$t('openYes'),
              duration:1000,
              onClose(){
                that.$router.push({
                  name:'adopt',
                })
              }
            })
          }else {
            localStorage.setItem('status','-1')
            Toast({
              message:that.$t('noPass'),
              duration:1000,
              onClose(){
                that.$router.push({
                  name:'notPass',
                })
              }
            })
          }
        }
        if(this.redata.action === "documentary" ){
          if(this.redata.data.object.code === 0){
            Toast({
              duration:1500,
              position:'bottom',
              message:that.$t('documentaryS')
            })
          }else {
            that.followState = false
            Toast({
              duration:2000,
              position:'bottom',
              message:that.redata.data.object.msg+that.$t('documentaryN'),
            })
          }
        }
      }
    },
    // 跳转到开户
    linkOpen(){
      // console.log(this.status)
      let that = this
    if(this.status === '1'&& this.isDrawPass === '1'){
        Toast({
          position:'bottom',
          message:that.$t('openAdd'),
          duration:1000
        })
      }else {
      if(this.status === '1'){
        this.$router.push({
          name: 'adopt'
        })
      }else if(this.status === '3'){
        this.$router.push({
          name: 'toBeAudited'
        })
      }else if(this.status === '-1'){
        this.$router.push({
          name: 'notPass'
        })
      }else {
        this.$router.push({path: '/register'})
      }
    }
    },
    // 跳转到我的钱包
    linkMyWallet () {
      this.$router.push({path: '/myWallet'})
    },
    // 跳转到分享
    linkShare(){
      this.$router.push({
        name:'share'
      })
    },
    // 跳转到我的账户管理
    linkMyManage () {
      this.$router.push({name: 'myManage'})
    },
    // 跳转到常见问题
    linkProblem () {
      this.$router.push({name: 'problem'})
    },
    // 跳转至拨号界面
    callPhone () {
      // window.location.href = 'tel://123456789'
      this.$router.push({
        name:'service'
      })
    },
    // 退出登录
    linkLogout() {
      this.Logout()
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../style/my";
  .my-top-box1{
    height: tovw(230)!important;
  }
</style>



